import React, { useState, useEffect } from 'react';
import { fetchChatCompletion } from '../api/deepseek';
import Logout from './Logout';
import {
  Container,
  Box,
  TextField,
  MenuItem,
  Button,
  Typography,
  AppBar,
  Toolbar,
  Select,
  FormControl,
  InputLabel,
  IconButton,
  CircularProgress,
  Paper,
  Snackbar,
} from '@mui/material';
import { ContentCopy as ContentCopyIcon } from '@mui/icons-material';
import { auth } from '../firebase/config';
import './MainApp.css';
import './WixMadeforText.css'; // Include custom font

const MainApp = ({ user }) => {
  const [inputText, setInputText] = useState('');
  const [outputText, setOutputText] = useState('');
  const [mode, setMode] = useState('business');
  const [userName, setUserName] = useState('');
  const [copySuccess, setCopySuccess] = useState(false);
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    if (user) {
      setUserName(user.displayName || user.email);
    }
  }, [user]);

  const translateText = async () => {
    setLoading(true);
    const systemPrompt = mode === 'business'
      ? 'You are a professional assistant in a business environment.'
      : mode === 'email'
      ? 'You are writing a formal email.'
      : mode === 'resume'
      ? 'You are creating a professional resume.'
      : 'You are a friendly assistant chatting casually.';

    const messages = [
      { role: 'system', content: systemPrompt },
      { role: 'user', content: inputText },
    ];
    try {
      const response = await fetchChatCompletion(messages);
      setOutputText(response);

      if (window.gtag) {
        window.gtag('event', 'translate', {
          event_category: 'Text Translation',
          event_label: mode,
          value: inputText.length,
        });
      }
    } catch (error) {
      console.error('Error:', error);
    } finally {
      setLoading(false);
    }
  };

  const handleCopy = () => {
    navigator.clipboard.writeText(outputText).then(() => {
      setCopySuccess(true);
    });
  };

  const handleCloseSnackbar = () => {
    setCopySuccess(false);
  };

  return (
    <Container maxWidth="sm" sx={{ height: '100vh', overflow: 'auto' }}>
      {/* Change AppBar color to #3a97ad */}
      <AppBar position="static" sx={{ background: '#3a97ad' }}>
        <Toolbar>
          <Typography variant="h6" component="div" sx={{ flexGrow: 1, fontFamily: 'Wix Madefor Text', fontWeight: 'bold' }}>
            SoundNative
          </Typography>
          <Box sx={{ display: 'flex', flexDirection: 'column', alignItems: 'center', paddingRight: '16px' }}>
            <Typography variant="body1">{userName}</Typography>
            <Box sx={{ paddingTop: '5px' }}><Logout /></Box>
          </Box>
        </Toolbar>
      </AppBar>
      <Box sx={{ my: 4, p: 3, boxShadow: 3, borderRadius: 2, backgroundColor: '#fff', margin: 'auto', maxWidth: '600px', minHeight: 'calc(100vh - 128px)' }}>
        <Typography variant="h4" gutterBottom>
          Text Generator
        </Typography>
        <Typography variant="subtitle1" gutterBottom>
          Generate professional or casual text in seconds
        </Typography>
        <TextField
          label="Enter your text here"
          multiline
          fullWidth
          rows={4}
          value={inputText}
          onChange={(e) => setInputText(e.target.value)}
          variant="outlined"
          margin="normal"
        />
        <FormControl fullWidth margin="normal">
          <InputLabel id="mode-select-label">Mode</InputLabel>
          <Select
            labelId="mode-select-label"
            value={mode}
            label="Mode"
            onChange={(e) => setMode(e.target.value)}
          >
            <MenuItem value="business">Business</MenuItem>
            <MenuItem value="casual">Casual</MenuItem>
            <MenuItem value="email">Email</MenuItem>
            <MenuItem value="resume">Resume</MenuItem>
          </Select>
        </FormControl>
        {/* Change Button color to #3a97ad */}
        <Button variant="contained" color="primary" onClick={translateText} fullWidth sx={{ mb: 2, background: '#3a97ad' }}>
          Generate
        </Button>
        <Paper className="output-box" sx={{ mt: 2, p: 2, boxShadow: 1, borderRadius: 1, backgroundColor: '#f9f9f9', position: 'relative' }}>
          {/* Loading animation confined to output box */}
          {loading ? (
            <Box className="loading-container" sx={{ display: 'flex', justifyContent: 'center', alignItems: 'center', height: '100px', width: '100%' }}>
              <CircularProgress size={24} />
              <Typography variant="body1" sx={{ ml: 2 }}>Generating...</Typography>
            </Box>
          ) : (
            <Typography variant="body1">{outputText}</Typography>
          )}
          <IconButton
            onClick={handleCopy}
            className="copy-button"
          >
            <ContentCopyIcon />
          </IconButton>
        </Paper>
      </Box>
      <Snackbar
        open={copySuccess}
        autoHideDuration={3000}
        onClose={handleCloseSnackbar}
        message="Text copied to clipboard"
      />
    </Container>
  );
};

export default MainApp;
